@import "../../../../../_variables.scss";

.search {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	padding-right: 20px;
	width: 550px;
	height: 60px;
	background: $light;
	border: 1px solid $border;

	@media screen and (max-width: 750px) {
		width: 300px;
	}

	@media screen and (min-width: 750px) and (max-width: 1250px) {
		width: 400px;
	}

	form {
		width: 90%;

		input {
			border: 0px;
			width: 100%;
			height: 50px;

			&:focus {
				outline: none;
			}
		}

		input::placeholder {
			font-family: "Lato";
			font-style: normal;
			font-weight: 300;
			font-size: 16px;
			line-height: 19px;
		}
	}

	&_icon {
		position: relative;
	}
}
